*{
    margin: 0;
    padding: 0;
}

.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    /* display: flex;
    align-items: center;
    justify-content: center; */
    font-size: calc(10px + 2vmin);
    color: white;
}

.drag-a-drop{
    /* padding: 5rem; */
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: repeat(auto-fill, 300px);
    align-items: flex-start ;
}

.group-title{
    text-align: left;
    margin-bottom: .5rem;
    font-size: 1.5rem;
}

.dnd-group{
    background-color: #49505e;
    border-radius: 5px;
    padding: .5rem;
    gap: .5rem; 
    margin: 0 5px;
}

.dnd-item {
    min-height: 150px;
    background-color: #fff;
    color: #282c34;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
}

.current {
    background-color: #282c34;
}

.dnd-item:not(:last-of-type){
    margin-bottom: .5rem;

}

.dnd-item * {
    margin: 0;
}